import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useWindowSize } from 'react-use'
import ReactConfetti from 'react-confetti'
import './creditCardForm.scss'
import {
    setCreditCardCVV,
    setCreditCardExpirationDate,
    setCreditCardFlip,
    setCreditCardHolderName,
    setCreditCardNumber,
    setCreditCardType,
} from '../../../store/toolKitRedux/userDataSlice'

const defaultValidationData = {
    cardNumber: {isValid: true, message: ''},
    cardOwner: {isValid: true, message: ''},
    cardDate: {isValid: true, message: ''},
    cardCVC: {isValid: true, message: ''},
}

const CreditCardForm = () => {
    const creditCard = useSelector(state => state.userData.creditCard)
    const dispatch = useDispatch()
    const [submitted, setSubmitted] = useState(false)
    const {width, height} = useWindowSize()
    const [cardValidation, setCardValidation] = useState(Object.assign(defaultValidationData))


    const validateCardData = (data) => {
        const {cardNumber, cardOwner, cardDate, cardCVC} = data
        setCardValidation(Object.assign(defaultValidationData))

        if (cardNumber?.length < 16) {
            setCardValidation({
                ...cardValidation,
                cardNumber: {isValid: false, message: '  16 '},
            })
        }

        if (cardOwner?.length <= 0) {
            setCardValidation({
                ...cardValidation,
                cardOwner: {isValid: false, message: '   '},
            })
        }

        if (!cardDate) {
            setCardValidation({
                ...cardValidation,
                cardDate: {isValid: false, message: ' '},
            })
        }

        if(cardCVC >= 1000){
            setCardValidation({
                ...cardValidation,
                cardCVC: {isValid: false, message: ' 3-  cvc'},
            })
        }
    }

    useEffect(()=>{
        if(creditCard){
            console.log("validate")
            validateCardData(creditCard)
        }
    },[creditCard])

    const flipCard = () => {
        dispatch(setCreditCardFlip(true))
    }
    const unFlipCard = () => {
        dispatch(setCreditCardFlip(false))
    }
    const checkSubstring = (length, match) => {
        return creditCard.cardNumber.substring(0, length) === match
    }

    const setNumber = (e) => {
        let cardNumber = e.target.value
        let cardType = ''
        if (cardNumber[0] === '4') {
            cardType = 'Visa'
        } else if (checkSubstring(4, '6011')) {
            cardType = 'Discover'
        } else if (checkSubstring(2, '51')) {
            cardType = 'MasterCard'
        } else {
            cardType = ''
        }
        dispatch(setCreditCardType(cardType))
        dispatch(setCreditCardNumber(cardNumber))
    }
    const setName = (e) => {
        const cardHolderName = e.target.value.toUpperCase()
        dispatch(setCreditCardHolderName(cardHolderName))
    }
    const setDate = (e) => {
        let data = (e.target.value).split('')
        let cardExpirationDate = (data.map((x) => {
            return x === '-' ? '/' : x
        })).join('')
        dispatch(setCreditCardExpirationDate(cardExpirationDate))
    }
    const setCVV = (e) => {
        let cardCVV = e.target.value
        dispatch(setCreditCardCVV(cardCVV))
    }

    const onSubmit = (e) => {
        e.preventDefault()

        const data = new FormData(e.currentTarget)
        console.log(data)

        setSubmitted(true)
    }

    return (
        <form
            className="card-form"
            onSubmit={(e) => onSubmit(e)}
        >
            {submitted ?
                <ReactConfetti
                    width={width}
                    height={height}
                /> :
                null}
            <label className="input-label">
                {' '}
            </label>
            <input
                type="text"
                placeholder={'  '}
                value={creditCard.cardNumber}
                id="number-input"
                name="cardNumber"
                className="text-input"
                maxLength="16"
                onChange={setNumber}
            />
            <label className="input-label">
                {' '} {cardValidation.cardOwner.message}
            </label>
            <input
                type="text"
                placeholder={'   '}
                value={creditCard.cardHolderName}
                name="cardOwner"
                onChange={e => setName(e)}
                className="text-input"
                maxLength="30"
            />
            <div className="date-and-csv">
                <div className="date-and-csv__date">
                    <label className="input-label">
                        {'  '}
                    </label>
                    <input
                        type="month"
                        name="cardDate"
                        placeholder="   "
                        className="text-input"
                        onChange={e => setDate(e)}
                    />
                </div>
                <div className="date-and-csv__cvc">
                    <label className="input-label">
                        {'CVC'}
                    </label>
                    <input
                        type="text"
                        placeholder={' CVC'}
                        maxLength="3"
                        value={creditCard.cardCVV}
                        name="cardCVC"
                        className="text-input"
                        onChange={e => setCVV(e)}
                        onFocus={flipCard}
                        onBlur={unFlipCard}
                    />
                </div>
            </div>

            <button
                className="card-form__submit-button"
                type="submit"
            >
                {''}
            </button>
        </form>
    )
}

export default CreditCardForm